<template>
  <div :class="{ 'main': true, wide: wide }">
    <slot />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'GridContent',
  computed: {
    ...mapGetters('theme', {
      contentWidth: 'contentWidth'
    }),
    wide () {
      return this.contentWidth === 'Fixed'
    }
  }

}
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  min-height: 100%;
  transition: 0.3s;
  &.wide {
    max-width: 1200px;
    margin: 0 auto;
  }
}
</style>
